body {
    background-color: #010101;
    font-family: 'Montserrat', sans-serif;
    margin: 0px;
    padding: 0px;
    color: #ccc;
    font-size: 12px;
    -webkit-font-smoothing: subpixel-antialiased;
    -webkit-text-size-adjust: none;
    letter-spacing: .05em;
}

svg {
    display: block;
    margin: 0px auto;
    clear: both;
}

#links {
    text-align: center;
    font-size: 12px;
    margin-top: 60px;
    font-weight: 400;
}

#links a {
    margin: 5px 15px;
    position: relative;
}

#links .donate::after {
    content: '\2665';
    color: #8A0000;
    position: absolute;
    margin-top: -5px;
    margin-left: 0px;
    font-size: 10px;
}

a {
    color: #158FC9;
    cursor: pointer;
    text-decoration: none;
    border-bottom: 3px solid #1c1c1c;
    -webkit-transition: border 100ms ease;
    -o-transition: border 100ms ease;
    transition: border 100ms ease;
}

a:hover {
    border-bottom-color: white;
}

#what-is {
    font-weight: 200;
    width: 250px;
    text-align: justify;
    margin: 40px auto 0px;
    line-height: 1.9;
}

#what-is .filetype {
    padding: 0px 5px;
    background-color: #222;
    border-radius: 3px;
}

#crafted-by {
    text-align: center;
    font-size: 11px;
    color: #736c6c;
    width: 200px;
    margin: 40px auto;
    line-height: 1.7;
    font-weight: 300;
}

#crafted-by a {
    text-decoration: none;
    border-width: 0px 0px 1px;
    border-style: solid;
    cursor: pointer;
    color: inherit;
    -webkit-transition: all 100ms ease;
    -o-transition: all 100ms ease;
    transition: all 100ms ease;
}

#crafted-by a:hover {
    color: white;
    border-bottom: 4px solid rgba(255, 255, 255, 0.25);
}

#tweet-me {
    position: fixed;
    top: 15px;
    right: 15px;
    color: #fff;
    font-size: 12px;
    padding: 5px 10px;
    border-radius: 3px;
    border: none;
    background-color: #158FC9;
    cursor: pointer;
}

#tweet-me:hover {
    background-color: #21aee7;
}

.App {
    height: 100vh;
    overflow: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-overflow-scrolling: touch;
}

.content {
    margin: auto;
    background: rgba(0, 0, 0, 0.8);
    -webkit-box-shadow: 0 10px 100px black;
            box-shadow: 0 10px 100px black;
    z-index: 10;
}

#svgWrapper {
    text-align: center;
    width: 150px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 3px;
    padding: 15px;
}

#logoSVG.clicked {
    visibility: hidden;
}

